.header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-bottom: 0.4rem solid #40c057;
  box-shadow: 0 0 0.3rem #0000003d;
  height: 6.6rem;
  padding: 0 4.8rem;
  width: 100%;
  gap: 80rem;
}

.logo {
  height: 6.2rem;
  padding: 1.2rem;
  transition: all 0.2s;
}

.logo:hover,
.logo:active {
  transform: scale(1.1);
  transition: all 0.2s;
}

/* NAVIGATION */

.main-nav-list {
  list-style: none;
  display: flex;
  gap: 2.4rem;
}

.scale-up {
  transition: all 0.2s;
}

.main-nav-link {
  background-color: #40c057;
  padding: 0.8rem;
  border-radius: 10px;
  font-weight: 450;
  text-decoration: none;
  color: #fff;
  font-size: 1.8rem;
  transition: all 0.2s;
}

.sign-up {
  background-color: #ffb300;
}

.scale-up:hover,
.scale-up:active {
  transform: scale(1.1);
  transition: all 0.2s;
}
